import React from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  Paper,
  Table,
  TableHead,
  TableContainer,
  TableRow,
  TableBody,
  TableCell,
  Input,
  InputLabel,
  InputAdornment,
  FormControl,
} from '@mui/material';
import BackArrow from '../components/backArrow';
import BottomBar from '../components/bottomBar';
import ListingSmall from '../components/listingSmall';
import ProfileMini from '../components/profileMini';

const PurchaseHistory = () => {
  return (
    <div className="bg-gray-100 flex flex-col justify-items-stretch">
      {/* Top Navigation Bar */}
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <BackArrow />
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            Purchase History
          </Typography>
        </Toolbar>
      </AppBar>

      {/* Main Content */}
      <Paper sx={{ width: '100%', overflow: 'hidden' }}>
        <TableContainer sx={{ maxHeight: 640 }}>
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              <TableRow>
                <TableCell
                  key="column-item"
                  align="left"
                  style={{ minWidth: '125px' }}
                >
                  Item
                </TableCell>
                <TableCell
                  key="column-seller"
                  align="left"
                  style={{ minWidth: '70px' }}
                >
                  Seller
                </TableCell>
                <TableCell
                  key="column-transaction"
                  align="left"
                  style={{ minWidth: '70px' }}
                >
                  Transaction Details
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {[
                {
                  name: 'Vintage T-shirt',
                  price: '$40',
                  image:
                    'https://www.shutterstock.com/shutterstock/photos/2448662399/display_1500/stock-photo-cyan-summer-stile-shirt-isolated-on-white-blue-grren-t-shirt-flying-single-object-casual-unisex-2448662399.jpg',
                  user: 'https://i.imgur.com/O5CTjeF.jpeg',
                  userName: 'Ryan',
                },
                {
                  name: 'Waterbottle',
                  price: '$80',
                  image:
                    'https://www.shutterstock.com/shutterstock/photos/2490213671/display_1500/stock-photo-white-reusable-water-bottle-mockup-2490213671.jpg',
                  user: 'https://i.imgur.com/O5CTjeF.jpeg',
                  userName: 'Ryan',
                },
                {
                  name: 'iPhone 15 Pro',
                  price: '$1000',
                  image:
                    'https://www.shutterstock.com/shutterstock/photos/2361673219/display_1500/stock-photo-antalya-turkey-september-newly-released-natural-titanium-iphone-pro-mockup-set-with-2361673219.jpg',
                  user: 'https://i.imgur.com/O5CTjeF.jpeg',
                  userName: 'Ryan',
                },
                {
                  name: 'Textbook',
                  price: '$50',
                  image:
                    'https://www.shutterstock.com/shutterstock/photos/247874560/display_1500/stock-photo-blank-book-isolated-on-grey-to-replace-your-design-247874560.jpg',
                  user: 'https://i.imgur.com/O5CTjeF.jpeg',
                  userName: 'Ryan',
                },
                {
                  name: 'Sony Camera',
                  price: '$2000',
                  image:
                    'https://www.shutterstock.com/shutterstock/photos/1638641902/display_1500/stock-photo-berlin-germany-sony-alpha-a-iii-mirrorless-digital-camera-body-and-lens-sony-g-1638641902.jpg',
                  user: 'https://i.imgur.com/O5CTjeF.jpeg',
                  userName: 'Ryan',
                },
                {
                  name: 'Nike Shoes',
                  price: 'negotiable',
                  image:
                    'https://www.shutterstock.com/shutterstock/photos/2532847703/display_1500/stock-photo-milan-lombardy-italy-september-black-and-white-nike-dunk-high-sneakers-displayed-on-2532847703.jpg',
                  user: 'https://i.imgur.com/O5CTjeF.jpeg',
                  userName: 'Ryan',
                },
              ].map((item, index) => (
                <TableRow hover role="checkbox" tabIndex={-1} key={item.name}>
                  <TableCell
                    key={item.name + '-listing'}
                    align="left"
                    className="!px-0.5"
                  >
                    <ListingSmall
                      key={index}
                      imageUrl={item.image}
                      text={item.name}
                      price={item.price}
                    />
                  </TableCell>
                  <TableCell
                    key={item.name + '-seller'}
                    align="left"
                    className="!px-0.5"
                  >
                    <ProfileMini imageUrl={item.user} text={item.userName} />
                  </TableCell>
                  <TableCell
                    key={item.name + '-price'}
                    align="left"
                    className="!px-0.5"
                  >
                    <FormControl sx={{ m: 1 }} variant="standard">
                      <InputLabel htmlFor="standard-adornment-amount">
                        Price Paid
                      </InputLabel>
                      <Input
                        id="standard-adornment-amount"
                        defaultValue={
                          isNaN(parseInt(item.price.replace('$', '')))
                            ? 'negotiable'
                            : parseInt(item.price.replace('$', '')) - 15
                        }
                        startAdornment={
                          <InputAdornment position="start">$</InputAdornment>
                        }
                      />
                    </FormControl>
                    <p>On {new Date().toLocaleDateString()}</p>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>

      <BottomBar />
    </div>
  );
};

export default PurchaseHistory;
