import React, { useState } from 'react';
import './CreateListingPage.css';
import BottomBar from '../components/bottomBar';
import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  Button,
  TextField,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';

const CreateListingPage = () => {
  const [title, setTitle] = useState('');
  const [price, setPrice] = useState('');
  const [description, setDescription] = useState('');
  const [photo, setPhoto] = useState(null);

  const navigate = useNavigate();

  const handleSell = () => {
    const newListing = {
      date: new Date().toISOString().split('T')[0],
      title: title,
      sold: false,
      description,
      price: '$' + price,
      imageUrl: photo || 'https://via.placeholder.com/100',
    };

    const savedListings = JSON.parse(localStorage.getItem('listings')) || [];
    const updatedListings = [newListing, ...savedListings];
    localStorage.setItem('listings', JSON.stringify(updatedListings));

    alert('Listing created!');
    navigate('/your-listings');
  };

  const handlePhotoUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      setPhoto(URL.createObjectURL(file));
    }
  };

  return (
    <div className="flex flex-col grow">
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            Create Listing
          </Typography>
        </Toolbar>
      </AppBar>

      <Box className="main-box p-4 flex flex-col grow">
        <TextField
          label="Title"
          variant="outlined"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          fullWidth
          margin="normal"
        />

        <TextField
          label="Price"
          variant="outlined"
          type="number"
          value={price}
          onChange={(e) => setPrice(e.target.value)}
          fullWidth
          margin="normal"
        />

        <TextField
          label="Description"
          variant="outlined"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          multiline
          rows={4}
          fullWidth
          margin="normal"
        />

        <div className="items-center w-full">
          {photo && <img className="w-full" src={photo} alt="Selected" />}
          <Button
            className="w-full"
            variant="outlined"
            component="label"
            sx={{ mt: 2 }}
          >
            Upload Photo
            <input
              type="file"
              hidden
              accept="image/*"
              onChange={handlePhotoUpload}
            />
          </Button>
        </div>

        <Button
          variant="contained"
          color="primary"
          onClick={handleSell}
          className="w-full"
          sx={{ mt: 2 }}
        >
          Sell
        </Button>
      </Box>
      <BottomBar />
    </div>
  );
};

export default CreateListingPage;
