import React from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { BottomNavigation, BottomNavigationAction } from '@mui/material';
import { Link } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import ChatIcon from '@mui/icons-material/Chat';
import PersonIcon from '@mui/icons-material/Person';
import SettingsIcon from '@mui/icons-material/Settings';

// Create a theme with overrides for BottomNavigationAction
const theme = createTheme({
  components: {
    MuiBottomNavigationAction: {
      styleOverrides: {
        root: {
          minWidth: 0, // Set minimum width to 0
          padding: '6px 8px', // Optional padding to keep icons visible
        },
      },
    },
  },
});

const BottomBar = () => {
  return (
    <ThemeProvider theme={theme}>
      <div className="w-full">
        <BottomNavigation showLabels className="bg-white shadow-inner w-full">
          <BottomNavigationAction
            label="Home"
            icon={<HomeIcon />}
            component={Link}
            to="/home"
          />
          <BottomNavigationAction
            label="Sell"
            icon={<AddCircleIcon />}
            component={Link}
            to="/create-listing"
          />
          <BottomNavigationAction
            label="Chats"
            icon={<ChatIcon />}
            component={Link}
            to="/chats"
          />
          <BottomNavigationAction
            label="Me"
            icon={<PersonIcon />}
            component={Link}
            to="/me"
          />
          <BottomNavigationAction
            label="Settings"
            icon={<SettingsIcon />}
            component={Link}
            to="/settings"
          />
        </BottomNavigation>
      </div>
    </ThemeProvider>
  );
};

export default BottomBar;
