import React, { useState } from 'react';
import './FAQPage.css';

const FAQPage = () => {
  const [message, setMessage] = useState('');
  const [sent, setSent] = useState(false);

  const faqs = [
    {
      question: 'How can I reset my password?',
      answer: "Go to the settings page and click on 'Reset Password'.",
    },
    {
      question: 'Where can I find my purchase history?',
      answer: "You can view your purchase history under 'My Orders' section.",
    },
    {
      question: 'How do I contact support?',
      answer: 'You can contact support by sending a message through this page.',
    },
  ];

  const handleSend = (e) => {
    e.preventDefault();
    setSent(true); // 
    setMessage(''); // 
    setTimeout(() => setSent(false), 2000); // 2
  };

  return (
    <>
      <div className="faq-page">
        <div className="faqtitle">FAQ / Support</div>

        <div className="faq-list">
          {faqs.map((faq, index) => (
            <div key={index} className="faq-item">
              <h4 className="faq-question">{faq.question}</h4>
              <p className="faq-answer">{faq.answer}</p>
            </div>
          ))}
        </div>

        <form className="message-form" onSubmit={handleSend}>
          <input
            type="text"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            placeholder="Type your question here..."
            className="message-input"
          />
          <button type="submit" className="send-button">
            Send
          </button>
        </form>

        {sent && (
          <p className="sent-confirmation">
            your message was successfully sent
          </p>
        )}
      </div>
    </>
  );
};

export default FAQPage;
