import React, { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import {
  Card,
  CardContent,
  Typography,
  Button,
  TextField,
  MenuItem,
} from '@mui/material';
import ProfileMini from '../components/profileMini';
import BottomBar from '../components/bottomBar';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import BackArrow from '../components/backArrow';

const Rating = () => {
  const location = useLocation();
  const navigate = useNavigate();

  const { userImage, userName } = location.state || {};

  // State for different view cards
  const [isPositive, setIsPositive] = useState(null);
  const [showSuccess, setShowSuccess] = useState(false);
  const [showInitial, setShowInitial] = useState(true);
  const [additionalComment, setAdditionalComment] = useState('');
  const [negativeReason, setNegativeReason] = useState('');

  // Options for negative review reasons
  const negativeOptions = [
    'Item not as described',
    'Slow shipping',
    'Poor communication',
  ];

  const handleUndo = () => {
    setShowSuccess(false);
    setIsPositive(null);
    setAdditionalComment('');
    setNegativeReason('');
    setShowInitial(true);
  };

  const handleSubmit = () => {
    setShowSuccess(true);
  };

  const handleBack = () => {
    navigate(-1);
  };

  const enterPositive = () => {
    setIsPositive(true);
    setShowInitial(false);
  };

  const enterNegative = () => {
    setIsPositive(false);
    setShowInitial(false);
  };

  return (
    <div>
      {/* Skeleton background */}
      <div
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          zIndex: 10,
          height: '745px',
          width: '339px',
          margin: '16px',
          padding: '0px',
          overflow: 'hidden',
        }}
      >
        {/* image: public/background.png */}
        <img
          src="./background.png"
          alt="background"
          style={{
            height: '745px',
            width: '339px',
            margin: '0px',
            padding: '0px',
            overflow: 'hidden',
          }}
        />
      </div>
      <div
        style={{
          backgroundColor: 'rgba(255, 255, 255, 0.5)',
          backdropFilter: 'blur(5px)',
          zIndex: 20,
          position: 'relative',
        }}
        className="flex flex-col justify-items-stretch w-[339px]"
      >
        {/* Top Navigation Bar */}
        <AppBar position="static" color="primary" elevation={1}>
          <Toolbar>
            <BackArrow />
            <Typography
              variant="h6"
              style={{ flexGrow: 1, textAlign: 'center' }}
            >
              Review
            </Typography>
          </Toolbar>
        </AppBar>
        <div className="min-h-[625px]">
          {/* Main Card for Review Option */}
          {!showSuccess && showInitial && (
            <Card sx={{ maxWidth: 400, width: '100%' }}>
              <CardContent>
                <Typography variant="h6">
                  Rate Purchase Experience with Seller
                </Typography>
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    marginTop: '16px',
                  }}
                >
                  <ProfileMini imageUrl={userImage} text={userName} />
                  <div style={{ display: 'flex', gap: '8px' }}>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={enterPositive}
                    >
                      +
                    </Button>
                    <Button
                      variant="contained"
                      color="secondary"
                      onClick={enterNegative}
                    >
                      -
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          )}

          {/* Positive Review Card */}
          {isPositive && !showSuccess && (
            <Card sx={{ maxWidth: 400, width: '100%' }}>
              <CardContent>
                <Typography variant="h6">Positive Review</Typography>
                <Typography variant="body1" gutterBottom>
                  Leave additional comment?
                </Typography>
                <TextField
                  fullWidth
                  label="Comment"
                  multiline
                  rows={3}
                  value={additionalComment}
                  onChange={(e) => setAdditionalComment(e.target.value)}
                  sx={{ marginBottom: 2 }}
                />
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleSubmit}
                  sx={{ marginRight: 1 }}
                >
                  Submit
                </Button>
                <Button
                  variant="outlined"
                  color="primary"
                  onClick={handleSubmit}
                >
                  Review w/o Comment
                </Button>
              </CardContent>
            </Card>
          )}

          {/* Negative Review Card */}
          {!isPositive && isPositive !== null && !showSuccess && (
            <Card sx={{ maxWidth: 400, width: '100%' }}>
              <CardContent>
                <Typography variant="h6">Negative Review</Typography>
                <Typography variant="body1" gutterBottom>
                  Reason for review
                </Typography>
                <TextField
                  select
                  fullWidth
                  label="Select Reason"
                  value={negativeReason}
                  onChange={(e) => setNegativeReason(e.target.value)}
                  sx={{ marginBottom: 2 }}
                >
                  {negativeOptions.map((option, index) => (
                    <MenuItem key={index} value={option}>
                      {option}
                    </MenuItem>
                  ))}
                </TextField>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleSubmit}
                  sx={{ marginRight: 1 }}
                >
                  Submit
                </Button>
              </CardContent>
            </Card>
          )}

          {/* Success Card */}
          {showSuccess && (
            <Card sx={{ maxWidth: 400, width: '100%' }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  Successfully left review for {userName}
                </Typography>
                <div
                  style={{
                    display: 'flex',
                    gap: '8px',
                    justifyContent: 'center',
                    marginTop: '16px',
                  }}
                >
                  <Button
                    variant="outlined"
                    color="primary"
                    onClick={handleUndo}
                  >
                    Undo
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleBack}
                  >
                    Okay
                  </Button>
                </div>
              </CardContent>
            </Card>
          )}
        </div>
        <BottomBar />
      </div>
    </div>
  );
};

export default Rating;
