import React, { useState } from 'react';
import {
  AppBar,
  Toolbar,
  IconButton,
  Box,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  List,
  ListItem,
  ListItemText,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import BottomBar from '../components/bottomBar';
import ListingSmall from '../components/listingSmall';

const Home = () => {
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const categories = [
    'All',
    'Clothing',
    'Electronics',
    'Books',
    'Sports',
    'Accessories',
  ];

  const handleMenuClick = () => {
    setIsDialogOpen(true);
  };

  const handleCategorySelect = (category) => {
    setIsDialogOpen(false);
  };

  const handleDialogClose = () => {
    setIsDialogOpen(false);
  };

  return (
    <div className="bg-gray-100 flex flex-col w-full">
      {/* Top Navigation Bar */}
      <AppBar position="static" className="bg-white text-black shadow-md">
        <Toolbar className="flex justify-between">
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={handleMenuClick}
          >
            <MenuIcon />
          </IconButton>
          <Box className="flex w-full items-center space-x-2">
            <TextField
              variant="outlined"
              size="small"
              placeholder="Search..."
              InputProps={{
                startAdornment: <SearchIcon className="mr-2" />,
              }}
            />
          </Box>
        </Toolbar>
      </AppBar>

      {/* Main Content */}
      <Box className="overflow-y-scroll p-4 grid grid-cols-2 gap-4">
        {[
          {
            name: 'Vintage T-shirt',
            price: '$40',
            image:
              'https://www.shutterstock.com/shutterstock/photos/2448662399/display_1500/stock-photo-cyan-summer-stile-shirt-isolated-on-white-blue-grren-t-shirt-flying-single-object-casual-unisex-2448662399.jpg',
          },
          {
            name: 'Waterbottle',
            price: '$80',
            image:
              'https://www.shutterstock.com/shutterstock/photos/2490213671/display_1500/stock-photo-white-reusable-water-bottle-mockup-2490213671.jpg',
          },
          {
            name: 'iPhone 15 Pro',
            price: '$1000',
            image:
              'https://www.shutterstock.com/shutterstock/photos/2361673219/display_1500/stock-photo-antalya-turkey-september-newly-released-natural-titanium-iphone-pro-mockup-set-with-2361673219.jpg',
          },
          {
            name: 'Textbook',
            price: '$50',
            image:
              'https://www.shutterstock.com/shutterstock/photos/247874560/display_1500/stock-photo-blank-book-isolated-on-grey-to-replace-your-design-247874560.jpg',
          },
          {
            name: 'Sony Camera',
            price: '$2000',
            image:
              'https://www.shutterstock.com/shutterstock/photos/1638641902/display_1500/stock-photo-berlin-germany-sony-alpha-a-iii-mirrorless-digital-camera-body-and-lens-sony-g-1638641902.jpg',
          },
          {
            name: 'Nike Shoes',
            price: 'negotiable',
            image:
              'https://www.shutterstock.com/shutterstock/photos/2532847703/display_1500/stock-photo-milan-lombardy-italy-september-black-and-white-nike-dunk-high-sneakers-displayed-on-2532847703.jpg',
          },
        ].map((item, index) => (
          <ListingSmall
            key={index}
            imageUrl={item.image}
            text={item.name}
            price={item.price}
          />
        ))}
      </Box>

      <BottomBar />

      {/* Category Selection Dialog */}
      <Dialog open={isDialogOpen} onClose={handleDialogClose}>
        <DialogTitle>Filter by Category</DialogTitle>
        <DialogContent>
          <List>
            {categories.map((category) => (
              <ListItem
                button
                key={category}
                onClick={() => handleCategorySelect(category)}
              >
                <ListItemText primary={category} />
              </ListItem>
            ))}
          </List>
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default Home;
