import React from 'react';
import { useNavigate } from 'react-router-dom';
import ThumbsUpDownIcon from '@mui/icons-material/ThumbsUpDown';
import { Avatar } from '@mui/material';

const ProfileMini = ({ imageUrl="https://i.imgur.com/O5CTjeF.jpeg", text="Ryan" }) => {
    const navigate = useNavigate();

    const handleClick = () => {
      navigate("/profile", { state: { imageUrl, text } });
    };
  
    return (
      <div 
        onClick={handleClick} 
        className="cursor-pointer flex flex-col items-center hover:bg-gray-100 p-4 rounded-lg transition-shadow shadow-md hover:shadow-lg"
      >
        <Avatar src={imageUrl} alt={text} />
        <span className="text-center mt-2 text-md font-medium">{text} <span className="text-green-700">97%</span><ThumbsUpDownIcon /> </span>
      </div>
    );
};

export default ProfileMini;
