import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';

import { AppBar, Toolbar, Typography, TextField } from '@mui/material';
import Button from '@mui/material/Button';
import UploadFileIcon from '@mui/icons-material/UploadFile';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import BackArrow from '../components/backArrow';
import BottomBar from '../components/bottomBar';
import ListingSmall from '../components/listingSmall';
import ProfileSmall from '../components/profileSmall';

const Chat = () => {
  const location = useLocation();
  const { listingImage, listingTitle, userImage, userName, listingPrice } =
    location.state || {
      listingImage: 'https://i.imgur.com/fcL9opE.jpeg',
      listingTitle: 'NES',
      userImage: 'https://i.imgur.com/O5CTjeF.jpeg',
      userName: 'Ryan',
    };

  const [messages, setMessages] = useState([
    { text: "Hello, I'm interested in this item.", sender: 'user' },
    { text: 'Great! Is the price alright?', sender: 'other' },
  ]);

  const [inputText, setInputText] = useState('');
  const [negotiatePrice, setNegotiatePrice] = useState(35);

  const handleFile = () => {
    // open file uploading, but do nothing with the file
    let fileInput = document.createElement('input');

    fileInput.type = 'file';

    fileInput.onchange = (e) => {
      handleSendMessage('');
    };

    fileInput.click();
  };

  const handleCamera = () => {
    // open camera, but do nothing with the image
    let fileInput = document.createElement('input');

    fileInput.type = 'file';
    fileInput.accept = 'image/*';
    fileInput.capture = 'camera';

    fileInput.onchange = (e) => {
      handleSendMessage('');
    };

    fileInput.click();
  };

  const handlePrice = () => {
    handleSendMessage('$' + negotiatePrice);
  };

  const handleCustom = () => {
    let number;

    // use popup to get custom price
    do {
      number = prompt('Enter a custom price');
    } while (number === null || isNaN(number));

    handleSendMessage('Can you do $' + number);
  };

  const handleSendMessage = (m) => {
    if (inputText.trim() !== '' || m !== undefined) {
      const temp = inputText || m;
      const newMessages = [...messages, { text: temp, sender: 'user' }];
      setMessages(newMessages);
      // scroll to bottom
      setTimeout(() => {
        let chatsDiv = document.getElementById('chatsDiv');
        chatsDiv.scrollTo(0, chatsDiv.scrollHeight);
      }, 100);
      setInputText(''); // Clear the input field
      setTimeout(() => {
        let response = 'Great';
        if (temp.toLowerCase().includes('where')) {
          response = 'At the mills library';
        }
        if (
          temp.toLowerCase().includes('condition') ||
          temp.toLowerCase().includes('quality')
        ) {
          response = "It's in good condition";
        }
        if (temp.toLowerCase().includes('when')) {
          response = "I'm available after 5";
        }
        if (temp.toLowerCase().includes('what')) {
          response = "I don't know";
        }
        if (temp.toLowerCase().includes('there')) {
          response = "I'll see you there";
        }
        if (temp.match(/\d+/g)) {
          response = 'Sure, I can do $' + temp.match(/\d+/g)[0];
        }
        if (temp.toLowerCase().includes('trade')) {
          response = "Sure I'm willing to trade";
        }
        if (temp.toLowerCase().includes('35')) {
          response = '$39';
          setNegotiatePrice('36');
        }
        if (temp.toLowerCase().includes('36')) {
          response = '$38';
          setNegotiatePrice('37');
        }
        if (temp.toLowerCase().includes('')) {
          response = 'hmm, I see what you mean now';
        }
        if (temp.toLowerCase().includes('')) {
          response = 'okay ya that makes sense';
        }
        if (
          ((temp.toLowerCase().includes('picture') ||
            temp.toLowerCase().includes('photo')) &&
            (temp.toLowerCase().includes('another') ||
              temp.toLowerCase().includes('more') ||
              temp.toLowerCase().includes('other') ||
              temp.toLowerCase().includes('you'))) ||
          temp.toLowerCase().includes('angle')
        ) {
          response = '';
        }

        setMessages([...newMessages, { text: response, sender: 'other' }]);
        // scroll to bottom
        setTimeout(() => {
          let chatsDiv = document.getElementById('chatsDiv');
          chatsDiv.scrollTo(0, chatsDiv.scrollHeight);
        }, 100);
      }, 1000);
    }
  };

  return (
    <div className="bg-gray-100 flex flex-col justify-items-stretch">
      {/* Top Navigation Bar */}
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <BackArrow />
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            Chat
          </Typography>
        </Toolbar>
      </AppBar>

      <div className="h-full">
        <div className="max-w-md mx-auto p-4 bg-gray-100 rounded-lg shadow-md space-y-4">
          {/* Header with Item and Seller Information */}
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <ListingSmall
                imageUrl={listingImage}
                text={listingTitle}
                price={listingPrice}
              />
            </div>
            <div className="flex items-center space-x-2">
              <ProfileSmall imageUrl={userImage} text={userName} />
            </div>
          </div>

          {/* Divider */}
          <hr className="border-gray-300" />

          {/* Chat Messages */}
          <div
            id="chatsDiv"
            className="flex flex-col space-y-2 overflow-y-auto h-64 bg-white p-2 rounded-md shadow-inner m"
          >
            {messages.map((message, index) => (
              <div
                key={index}
                style={{ marginLeft: message.sender === 'user' ? '0' : 'auto' }}
              >
                <span
                  className={`p-1 my-4 rounded-lg ${
                    message.sender === 'user'
                      ? 'bg-blue-100 text-blue-800'
                      : 'bg-gray-200 text-gray-800'
                  }`}
                >
                  {message.text}
                </span>
              </div>
            ))}
          </div>

          {/* Chat Input Bar */}
          <div className="flex items-center space-x-2">
            <TextField
              variant="outlined"
              fullWidth
              placeholder="Type your message..."
              size="small"
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === 'Enter') handleSendMessage();
              }}
            />
            <Button
              variant="contained"
              color="primary"
              onClick={handleSendMessage}
            >
              Send
            </Button>
          </div>
          {/* attachment bar */}
          <div className="flex items-center space-x-2">
            <Button variant="contained" color="primary" onClick={handleFile}>
              <UploadFileIcon />
            </Button>
            <Button variant="contained" color="primary" onClick={handleCamera}>
              <CameraAltIcon />
            </Button>
            <Button variant="contained" color="secondary" onClick={handlePrice}>
              ${negotiatePrice}
            </Button>
            <Button
              variant="contained"
              color="secondary"
              onClick={handleCustom}
            >
              custom
            </Button>
          </div>
        </div>
      </div>
      <BottomBar />
    </div>
  );
};

export default Chat;
