import React, { useState } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  List,
  ListItem,
  ListItemText,
  Divider,
  Collapse,
  Switch,
  FormControlLabel,
  Select,
  MenuItem,
  TextField,
} from '@mui/material';
import { ExpandLess, ExpandMore } from '@mui/icons-material';
import LockIcon from '@mui/icons-material/Lock';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import ColorLensIcon from '@mui/icons-material/ColorLens';
import BottomBar from '../components/bottomBar';
import HelpIcon from '@mui/icons-material/Help';
import SupportIcon from '@mui/icons-material/Support';

const Settings = () => {
  const [openSections, setOpenSections] = useState({
    privacy: false,
    account: false,
    customization: false,
  });

  const [expandedFAQ, setExpandedFAQ] = useState(Array(5).fill(false));

  const toggleSection = (section) => {
    setOpenSections((prevOpenSections) => ({
      ...prevOpenSections,
      [section]: !prevOpenSections[section],
    }));
  };

  const toggleFAQItem = (index) => {
    setExpandedFAQ((prev) =>
      prev.map((isOpen, idx) => (idx === index ? !isOpen : isOpen))
    );
  };

  return (
    <div className="bg-gray-100 flex flex-col justify-items-stretch w-full h-full">
      {/* Top Navigation Bar */}
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            Settings
          </Typography>
        </Toolbar>
      </AppBar>

      <div className="p-4 h-full overflow-y-scroll">
        {/* Privacy Settings Section */}
        <List>
          <ListItem button onClick={() => toggleSection('privacy')}>
            <ListItemText primary="Privacy" />
            <LockIcon />
            {openSections.privacy ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse in={openSections.privacy} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItem>
                <FormControlLabel
                  control={<Switch />}
                  label="Enable Location"
                />
              </ListItem>
              <ListItem>
                <FormControlLabel control={<Switch />} label="Show Full Name" />
              </ListItem>
              <ListItem>
                <FormControlLabel
                  control={<Switch />}
                  label="Show Online Status"
                />
              </ListItem>
            </List>
          </Collapse>
        </List>

        <Divider sx={{ my: 2 }} />

        {/* Account Settings Section */}
        <List>
          <ListItem button={true} onClick={() => toggleSection('account')}>
            <ListItemText primary="Account" />
            <AccountBoxIcon />
            {openSections.account ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse in={openSections.account} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItem>
                <TextField
                  label="Email"
                  variant="outlined"
                  fullWidth
                  defaultValue="user@example.com"
                />
              </ListItem>
              <ListItem>
                <FormControlLabel
                  control={<Switch />}
                  label="Two-Factor Authentication"
                />
              </ListItem>
              <ListItem>
                <Button variant="contained" color="secondary" fullWidth>
                  Change Password
                </Button>
              </ListItem>
            </List>
          </Collapse>
        </List>

        <Divider sx={{ my: 2 }} />

        {/* Customization Settings Section */}
        <List>
          <ListItem button onClick={() => toggleSection('customization')}>
            <ListItemText primary="Customization" />
            <ColorLensIcon />
            {openSections.customization ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse
            in={openSections.customization}
            timeout="auto"
            unmountOnExit
          >
            <List component="div" disablePadding>
              <ListItem>
                <FormControlLabel
                  control={<Switch />}
                  label="Enable Dark Mode"
                />
              </ListItem>
              <ListItem>
                <TextField
                  defaultValue={24}
                  label="Font Size"
                  type="number"
                  variant="outlined"
                  fullWidth
                />
              </ListItem>
              <ListItem>
                <Select fullWidth defaultValue="blue">
                  <MenuItem value="blue">Blue</MenuItem>
                  <MenuItem value="red">Red</MenuItem>
                  <MenuItem value="green">Green</MenuItem>
                </Select>
              </ListItem>
            </List>
          </Collapse>
        </List>

        <Divider sx={{ my: 2 }} />

        {/* FAQ Section */}
        <List>
          <ListItem button onClick={() => toggleSection('faq')}>
            <ListItemText primary="FAQ" />
            <HelpIcon />
            {openSections.faq ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse in={openSections.faq} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              {[
                'How can I change my password?',
                'How do I delete my account?',
                'How can I update my profile picture?',
                'What is two-factor authentication?',
                'How can I contact support?',
              ].map((question, index) => (
                <React.Fragment key={index}>
                  <ListItem button onClick={() => toggleFAQItem(index)}>
                    <ListItemText primary={question} />
                    {expandedFAQ[index] ? <ExpandLess /> : <ExpandMore />}
                  </ListItem>
                  <Collapse
                    in={expandedFAQ[index]}
                    timeout="auto"
                    unmountOnExit
                  >
                    <List component="div" disablePadding>
                      <ListItem>
                        <ListItemText
                          primary="Try to figure it out on your own."
                          secondary="You were smart enough to get into university weren't you?"
                        />
                      </ListItem>
                    </List>
                  </Collapse>
                </React.Fragment>
              ))}
            </List>
          </Collapse>
        </List>

        <Divider sx={{ my: 2 }} />

        {/* Support Section */}
        <List>
          <ListItem button onClick={() => toggleSection('support')}>
            <ListItemText primary="Support" />
            <SupportIcon />
            {openSections.support ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse in={openSections.support} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItem>
                <TextField
                  fullWidth
                  label="Subject"
                  variant="outlined"
                  sx={{ marginBottom: 2 }}
                />
              </ListItem>
              <ListItem>
                <TextField
                  fullWidth
                  label="Describe your issue"
                  multiline
                  rows={4}
                  variant="outlined"
                />
              </ListItem>
              <ListItem>
                <Button variant="contained" color="primary" fullWidth>
                  Submit Ticket
                </Button>
              </ListItem>
            </List>
          </Collapse>
        </List>
      </div>

      <BottomBar />
    </div>
  );
};

export default Settings;
