import React, { useEffect, useState } from 'react';
import './YourListPage.css';
import BackArrow from '../components/backArrow';
import { AppBar, Box, Toolbar, Typography } from '@mui/material';
import BottomBar from '../components/bottomBar';
import { Link } from 'react-router-dom';

const initialListings = [
  {
    date: '2024-11-01',
    title: 'Snowboard',
    price: '$1000',
    sold: false,
    imageUrl:
      'https://scene7.zumiez.com/is/image/zumiez/product_main_medium/Aperture-Quantum-Snowboard-2024-_184037-CA.jpg',
  },
  {
    date: '2024-10-25',
    title: 'Ski Boots',
    price: '$250',
    sold: true,
    imageUrl:
      'https://www.switchbacktravel.com/sites/default/files/articles%20/Ski%20Boots%20%28Tecnica%20Mach1%20120%20in%20snow%20-%20m%29.jpg',
  },
  {
    date: '2024-10-20',
    title: 'Helmet',
    price: '$150',
    sold: true,
    imageUrl:
      'data:image/jpeg;base64,',
  },
];

const YourListPage = () => {
  const [listings, setListings] = useState([]);

  useEffect(() => {
    const savedListings = JSON.parse(localStorage.getItem('listings')) || [];
    setListings([...savedListings, ...initialListings]);
  }, []);

  return (
    <div className="flex flex-col grow">
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <BackArrow />
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            Your Listings
          </Typography>
        </Toolbar>
      </AppBar>
      <Box className="overflow-y-scroll flex-1" style={{ width: '100%' }}>
        <div className="listings">
          {listings.map((item, index) => (
            <Link
              to="/view-listing"
              state={{
                imageUrl: item.imageUrl,
                text: item.title,
                price: item.price,
                sold: item.sold,
                description: item.description,
              }}
            >
              <div className="listing-item" key={index}>
                {item.imageUrl.length > 0 ? (
                  <img
                    src={item.imageUrl}
                    alt="Listing"
                    className="listing-image"
                  />
                ) : (
                  <img
                    src={'https://via.placeholder.com/50'}
                    alt="Listing"
                    className="listing-image"
                  />
                )}
                <div className="listing-info">
                  <p className="listing-date">{item.date}</p>
                  <p className="listing-title">{item.title}</p>
                </div>
                {item.sold && <div className="sold-label">Sold</div>}
              </div>
            </Link>
          ))}
        </div>
      </Box>
      <BottomBar />
    </div>
  );
};

export default YourListPage;
