import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import {
  AppBar,
  Toolbar,
  Typography,
  Tabs,
  Tab,
  List,
  ListItem,
  ListItemText,
  Badge,
  Button,
  Paper,
  Divider,
} from '@mui/material';
import ChatIcon from '@mui/icons-material/Chat';
import BackArrow from '../components/backArrow';
import BottomBar from '../components/bottomBar';
import ListingSmall from '../components/listingSmall';
import ProfileMini from '../components/profileMini';

const fakeChats = [
  {
    id: 1,
    listing: { imageUrl: 'https://i.imgur.com/fcL9opE.jpeg', title: 'NES' },
    user: { imageUrl: 'https://i.imgur.com/O5CTjeF.jpeg', name: 'Ryan' },
    lastMessage: "Hey! I'm interested in this item.",
  },
  {
    id: 2,
    listing: {
      imageUrl:
        'https://www.shutterstock.com/shutterstock/photos/2448662399/display_1500/stock-photo-cyan-summer-stile-shirt-isolated-on-white-blue-grren-t-shirt-flying-single-object-casual-unisex-2448662399.jpg',
      title: 'Vintage T-shirt',
    },
    user: {
      imageUrl:
        'https://m.media-amazon.com/images/M/MV5BZjYyZTc3MzEtZTk0OC00NWNlLWJmOTMtYjFmOGY1YjZlNjI5XkEyXkFqcGc@._V1_QL75_UY140_CR55,0,140,140_.jpg',
      name: 'Dave',
    },
    lastMessage: 'Is it still available?',
  },
  {
    id: 3,
    listing: {
      imageUrl:
        'https://www.shutterstock.com/shutterstock/photos/2490213671/display_1500/stock-photo-white-reusable-water-bottle-mockup-2490213671.jpg',
      title: 'Waterbottle',
    },
    user: { imageUrl: 'https://i.imgur.com/O5CTjeF.jpeg', name: 'Ryan' },
    lastMessage: 'I can meet you at the library.',
  },
  {
    id: 4,
    listing: {
      imageUrl:
        'https://www.shutterstock.com/shutterstock/photos/2361673219/display_1500/stock-photo-antalya-turkey-september-newly-released-natural-titanium-iphone-pro-mockup-set-with-2361673219.jpg',
      title: 'iPhone 15 Pro',
    },
    user: {
      imageUrl:
        'https://m.media-amazon.com/images/M/MV5BMTczMjUwNDUzMF5BMl5BanBnXkFtZTgwNDA4OTAzMTE@._V1_QL75_UX140_CR0,12,140,140_.jpg',
      name: 'Jared',
    },
    lastMessage: 'Ill see you there',
  },
  {
    id: 5,
    listing: {
      imageUrl:
        'https://www.shutterstock.com/shutterstock/photos/247874560/display_1500/stock-photo-blank-book-isolated-on-grey-to-replace-your-design-247874560.jpg',
      title: 'Textbook',
    },
    user: { imageUrl: 'https://i.imgur.com/O5CTjeF.jpeg', name: 'Ryan' },
    lastMessage: 'Okay',
  },
  {
    id: 6,
    listing: {
      imageUrl:
        'https://www.shutterstock.com/shutterstock/photos/1638641902/display_1500/stock-photo-berlin-germany-sony-alpha-a-iii-mirrorless-digital-camera-body-and-lens-sony-g-1638641902.jpg',
      title: 'Camera',
    },
    user: {
      imageUrl:
        'https://m.media-amazon.com/images/M/MV5BOTljNDQ1MjEtMmIyZS00YWFkLWFhYmEtODdmZWFkNzA5YzY5XkEyXkFqcGc@._V1_QL75_UX820_.jpg',
      name: 'Ana',
    },
    lastMessage: 'I am interested in this item',
  },
  // Add more chat data as needed
];

const Chats = () => {
  const navigate = useNavigate();

  const [primaryTab, setPrimaryTab] = useState(0);
  const [secondaryTab, setSecondaryTab] = useState(0);

  const handlePrimaryTabChange = (_, newValue) => {
    setPrimaryTab(newValue);
    setSecondaryTab(0); // Reset the secondary tab when switching sections
  };

  const handleSecondaryTabChange = (_, newValue) => {
    setSecondaryTab(newValue);
  };

  const openChat = (listingImage, listingTitle, userImage, userName) => () => {
    navigate('/chat', {
      state: { listingImage, listingTitle, userImage, userName },
    });
  };

  const leaveReview = (userImage, userName) => () => {
    navigate('/review', {
      state: { userImage, userName },
    });
  };

  const renderChatsList = (status) => {
    const randomChats = fakeChats
      .sort(() => 0.5 - Math.random()) // Randomize the array
      .slice(0, Math.floor(1 + Math.random() * 5)); // Take the first 3 items

    return (
      <List>
        {randomChats.map((chat, index) => (
          <ListItem
            key={chat.id}
            component="div"
            className={status === 'complete' ? 'bg-gray-200' : ''}
            sx={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'flex-start',
              padding: '8px',
            }}
          >
            {/* Listing and Profile */}
            <div
              style={{ display: 'flex', alignItems: 'center', width: '100%' }}
            >
              <ListingSmall
                imageUrl={chat.listing.imageUrl}
                text={chat.listing.title}
              />
              <div className="mx-4">
                <ProfileMini
                  imageUrl={chat.user.imageUrl}
                  text={chat.user.name}
                />
              </div>
            </div>

            {/* Chat Preview and Review Button */}
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                marginTop: '8px',
                width: '100%',
              }}
            >
              <ListItemText
                primary={
                  <div
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                      cursor: 'pointer',
                    }}
                  >
                    <Button
                      size="small"
                      onClick={openChat(
                        chat.listing.imageUrl,
                        chat.listing.title,
                        chat.user.imageUrl,
                        chat.user.name
                      )}
                      variant="outlined"
                    >
                      <ChatIcon fontSize="small" />
                    </Button>
                    {chat.lastMessage}
                  </div>
                }
                className="flex-grow text-sm text-gray-600 font-medium truncate overflow-hidden whitespace nowrap w-10 mr-2"
              />
              {status === 'awaiting review' && (
                <Badge color="secondary" variant="dot">
                  <Button
                    variant="outlined"
                    onClick={leaveReview(chat.user.imageUrl, chat.user.name)}
                    sx={{ marginLeft: '8px' }}
                  >
                    Review
                  </Button>
                </Badge>
              )}
            </div>
            {index < randomChats.length - 1 && (
              <Divider sx={{ width: '100%', marginY: 1 }} />
            )}
          </ListItem>
        ))}
      </List>
    );
  };

  return (
    <div className="bg-gray-100 flex flex-col justify-items-stretch">
      {/* Top Navigation Bar */}
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <BackArrow />
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            Chats
          </Typography>
        </Toolbar>
      </AppBar>

      {/* Primary Tabs for Selling and Buying */}
      <Tabs value={primaryTab} onChange={handlePrimaryTabChange} centered>
        <Tab label="Selling" />
        <Tab label="Buying" />
      </Tabs>

      {/* Secondary Tabs for Status (Awaiting Review, Active, Complete) */}
      <Tabs value={secondaryTab} onChange={handleSecondaryTabChange} centered>
        <Tab
          label={
            <Badge
              badgeContent="" // Example badge count
              color="secondary"
            >
              Awaiting Review
            </Badge>
          }
        />
        <Tab label="Active" />
        <Tab label="Complete" />
      </Tabs>

      {/* Chat Lists */}
      <Paper elevation={1} className="p-4 h-full overflow-y-scroll">
        {secondaryTab === 0 && renderChatsList('awaiting review')}
        {secondaryTab === 1 && renderChatsList('active')}
        {secondaryTab === 2 && renderChatsList('complete')}
      </Paper>

      <BottomBar />
    </div>
  );
};

export default Chats;
