import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, Button, IconButton } from '@mui/material';
import { Favorite, FavoriteBorder } from '@mui/icons-material';
import BackArrow from '../components/backArrow';
import BottomBar from '../components/bottomBar';
import { useLocation, useNavigate } from 'react-router-dom';

const ViewListing = () => {
  const location = useLocation();
  const navigate = useNavigate();

  const { imageUrl, text, price, sold, description } = location.state || {
    imageUrl: 'https://i.imgur.com/fcL9opE.jpeg',
    text: 'NES',
    price: '$40',
    sold: false,
  };

  const [isFavorited, setIsFavorited] = useState(false);

  const handleFavoriteClick = () => {
    setIsFavorited(!isFavorited);
  };

  const handleContactSeller = () => {
    navigate('/chat', {
      state: {
        listingImage: imageUrl,
        listingTitle: text,
        listingPrice: price,
      },
    });
  };

  return (
    <div className="bg-gray-100 flex flex-col w-full">
      {/* Top Navigation Bar */}
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <BackArrow />
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            {text}
          </Typography>
        </Toolbar>
      </AppBar>

      {/* Main Content */}

      {/* temporary */}
      <div className="flex flex-col grow overflow-scroll space-y-4 p-4">
        <img src={imageUrl} alt="Listing" className="rounded-lg" />
        <p>
          <b>Price: </b>
          {price}
        </p>
        <h3>
          <b>Description:</b>
        </h3>
        {description ? (
          <p>{description}</p>
        ) : (
          <p>
            Discover the perfect blend of quality and convenience with this
            versatile product, designed to meet your everyday needs. Whether
            you're at home, work, or on the go, enjoy reliable performance,
            sleek design, and user-friendly features that make life easier.
          </p>
        )}
      </div>
      {/* temporary */}

      {!sold && (
        <div className="flex items-center justify-around p-4 border-t">
          <IconButton className="w-1/5" onClick={handleFavoriteClick}>
            {isFavorited ? <Favorite color="error" /> : <FavoriteBorder />}
          </IconButton>
          <Button
            className="w-4/5"
            variant="contained"
            color="primary"
            onClick={handleContactSeller}
          >
            Contact Seller
          </Button>
        </div>
      )}

      <BottomBar />
    </div>
  );
};

export default ViewListing;
