import React from 'react';
import { AppBar, Toolbar, Typography, Button } from '@mui/material';
import BottomBar from '../components/bottomBar';
import { useNavigate } from 'react-router-dom';

const Me = () => {
  const navigate = useNavigate();

  const handleProfileClick = () => {
    navigate('/profile', {
      state: {
        imageUrl:
          'https://www.shutterstock.com/shutterstock/photos/2470054311/display_1500/stock-vector-avatar-gender-neutral-silhouette-vector-illustration-profile-picture-no-image-for-social-media-2470054311.jpg',
        text: 'You',
      },
    });
  };

  return (
    <div className="bg-gray-100 flex flex-col w-full justify-items-stretch">
      {/* Top Navigation Bar */}
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            Me
          </Typography>
        </Toolbar>
      </AppBar>

      {/* Main Content */}

      <div className="flex flex-col grow space-y-4 p-4">
        <Button variant="outlined" onClick={handleProfileClick}>
          My Profile
        </Button>
        <Button
          variant="outlined"
          onClick={() => {
            navigate('/purchase-history');
          }}
        >
          Purchase History
        </Button>
        <Button
          variant="outlined"
          onClick={() => {
            navigate('/your-listings');
          }}
        >
          Your Listings
        </Button>
      </div>

      <BottomBar />
    </div>
  );
};

export default Me;
