import React from 'react';
import { AppBar, Button, Toolbar, Typography } from '@mui/material';
import BackArrow from '../components/backArrow';
import BottomBar from '../components/bottomBar';
import { useLocation } from 'react-router-dom';

const Profile = () => {
  const location = useLocation();
  const { imageUrl, text } = location.state || {
    imageUrl: 'https://experts.mcmaster.ca/harvestedImages/fullImages/1479.jpg',
    text: 'Chad',
  };

  return (
    <div className="flex flex-col w-full bg-gray-100 justify-items-stretch">
      {/* Top Navigation Bar */}
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <BackArrow />
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            {text === 'you' ? 'Your ' : ''}Profile
          </Typography>
        </Toolbar>
      </AppBar>

      {/* Main Content */}

      <div className="flex flex-col grow space-y-4 p-4">
        <img src={imageUrl} alt="Profile" className="rounded-lg" />
        <p className="text-lg font-semibold">{text}</p>

        {/* Additional Information */}
        <div className="space-y-2">
          <p>
            <strong>Username:</strong> john_doe
          </p>
          <p>
            <strong>Ratings:</strong> 4.5/5
          </p>
          <p>
            <strong>McMaster Email Address:</strong> johndoe@mcmaster.ca
          </p>
        </div>
      </div>

      {text === 'You' && (
        <Button
          variant="contained"
          color="primary"
          fullWidth
          onClick={() => (window.location.href = '/profile-edit')}
        >
          Edit Profile
        </Button>
      )}

      <BottomBar />
    </div>
  );
};

export default Profile;
