import React, { useState } from 'react';
import { AppBar, Toolbar, Typography } from '@mui/material';
import BackArrow from '../components/backArrow';
import BottomBar from '../components/bottomBar';
import './ProfilePage.css';

const ProfilePage = () => {
  const [profile, setProfile] = useState({
    name: 'John Doe',
    description: 'A student who loves coding.',
    address: '123 Example St, Tokyo',
    studentId: '123456',
    bio: 'Aspiring web developer',
  });

  const icons = [
    'https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png',
    'https://cdn.pixabay.com/photo/2016/08/08/09/17/avatar-1577909_1280.png',

    'https://cdn.pixabay.com/photo/2014/04/03/10/32/businessman-310819_1280.png',
  ];

  const [selectedIcon, setSelectedIcon] = useState(icons[0]);
  const [editingField, setEditingField] = useState(null);

  const handleEdit = (field) => {
    setEditingField(field);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProfile((prevProfile) => ({
      ...prevProfile,
      [name]: value,
    }));
  };

  const handleSave = () => {
    setEditingField(null);
  };

  return (
    // <div className="flex flex-col h-full bg-gray-100">
    <div className="flex flex-col w-full bg-gray-100 justify-items-stretch">
      {/* Top Navigation Bar */}
      <AppBar position="static" color="primary" elevation={1}>
        <Toolbar>
          <BackArrow />
          <Typography variant="h6" style={{ flexGrow: 1, textAlign: 'center' }}>
            Profile
          </Typography>
        </Toolbar>
      </AppBar>

      {/* Main Content */}

      <div className="icon-section">
        <img src={selectedIcon} alt="Profile Icon" className="profile-icon" />
        <button className="icon-edit-button" onClick={() => handleEdit('icon')}>
          <span role="img" aria-label="edit">
            
          </span>
        </button>
        {editingField === 'icon' && (
          <div className="icon-options">
            {icons.map((icon, index) => (
              <img
                key={index}
                src={icon}
                alt={`Icon ${index + 1}`}
                className={`icon-option ${
                  selectedIcon === icon ? 'selected' : ''
                }`}
                onClick={() => {
                  setSelectedIcon(icon);
                  setEditingField(null); // 
                }}
              />
            ))}
          </div>
        )}
      </div>

      <div className="flex grow flex-col  p-4 profile-fields">
        {['name', 'description', 'address', 'studentId', 'bio'].map((field) => (
          <div className="profile-field" key={field}>
            <span className="field-label p-4">
              {field.charAt(0).toUpperCase() + field.slice(1)}
            </span>
            {editingField === field ? (
              <textarea
                name={field}
                value={profile[field]}
                onChange={handleChange}
                onInput={(e) =>
                  (e.target.style.height = `${e.target.scrollHeight}px`)
                }
                className="field-input"
                rows="1"
              />
            ) : (
              <span className="field-value">{profile[field]}</span>
            )}
            <button
              className="edit-button"
              onClick={() =>
                editingField === field ? handleSave() : handleEdit(field)
              }
            >
              {editingField === field ? (
                'Save'
              ) : (
                <span role="img" aria-label="edit">
                  
                </span>
              )}
            </button>
          </div>
        ))}
      </div>

      <BottomBar />
    </div>
  );
};

export default ProfilePage;
