import React from 'react';
import { useNavigate } from 'react-router-dom';

const ListingSmall = ({
  imageUrl = 'https://i.imgur.com/fcL9opE.jpeg',
  text = 'NES',
  price = '$40',
}) => {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate('/view-listing', { state: { imageUrl, text, price } });
  };

  return (
    <div
      onClick={handleClick}
      className="cursor-pointer flex flex-col items-center hover:bg-gray-100 p-2 rounded-lg transition-shadow shadow-md hover:shadow-lg"
    >
      <img
        src={imageUrl}
        alt={text}
        className="w-32 h-32 rounded-lg object-cover"
      />
      <p className="text-center mt-2 text-sm font-small">{text}</p>
      <p className="text-center mt-2 text-xs font-small">{price}</p>
    </div>
  );
};

export default ListingSmall;
