import React from 'react';
import { useNavigate } from 'react-router-dom';
import ThumbsUpDownIcon from '@mui/icons-material/ThumbsUpDown';

const ProfileSmall = ({ imageUrl="https://i.imgur.com/O5CTjeF.jpeg", text="Ryan" }) => {
    const navigate = useNavigate();

    const handleClick = () => {
      navigate("/profile", { state: { imageUrl, text } });
    };
  
    return (
      <div 
        onClick={handleClick} 
        className="cursor-pointer flex flex-col items-center hover:bg-gray-100 p-4 rounded-lg transition-shadow shadow-md hover:shadow-lg"
      >
        <img 
          src={imageUrl} 
          alt={text} 
          className="w-32 h-32 rounded-lg object-cover"
        />
        <p className="text-center mt-2 text-lg font-medium">{text} <span className="text-green-700">97%</span><ThumbsUpDownIcon /> </p>
      </div>
    );
};

export default ProfileSmall;
